import React from "react";
import SearchPresenter from "./SearchPresenter";
import { moviesApi, tvApi } from 'api';


export default class extends React.Component {
  state = {
    movieResults:null,
    tvResults:null,
    searchTerm: "",
    error: null,
    loading: false
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const { searchTerm } = this.state;
    if(searchTerm !== ""){
      this.searchByTerm();
    }
  };

  updateTerm = event => {
    const { target:{value} } = event;
    console.log(value)
    this.setState({
      searchTerm: value
    })
  };

   searchByTerm = async () => {
    const { searchTerm } = this.state;
    this.setState({ loading:true }); // ì²«ì§¸ë¡œ ë¡œë”©ì„ í•´ì£¼ê³ 
    try{
      const {                                            // ê²°ê³¼ê°’ì´ ì¼ì¹˜í•˜ë©´ tryë¡œ ë°˜í™˜
        data:{results: movieResults}
      } = await moviesApi.search(searchTerm);
      const {
        data:{results: tvResults}              
      } = await tvApi.search(searchTerm);

      this.setState({movieResults,tvResults });
      
    }catch{
      this.setState({error:"Cant't find results."});      // ì¼ì¹˜í•˜ì§€ ì•Šìœ¼ë©´ ì—ëŸ¬ 
          
    }finally{
      this.setState({ loading: false });                  // ê²°ê³¼ê°€ tryë“  catchë“  ë§ˆì§€ë§‰ì—” finallyë¡œ ë°˜í™˜
    }
  }

  render(){
    const {movieResults,tvResults,searchTerm,error,loading} = this.state;
    return(
    <SearchPresenter 
    movieResults={movieResults}
    tvResults={tvResults}
    error={error}
    searchTerm={searchTerm}
    loading={loading}
    handleSubmit={this.handleSubmit}
    updateTerm={this.updateTerm}
    />
    );
  }

}